.room-standard {
  max-width: 1280px;
  margin-left: auto;
  margin-right: auto;
  background-color: $c-bgc-light;

  .hotel-room-description {
    width: 242px;

    @include tablet-s {
      width: 400px;
    }

    @include desktop {
      width: 400px;
    }
  }

  .hotel-room-photo {
    margin-left: auto;
    margin-right: auto;

    @include mobile-s {
      margin-left: auto;
      margin-right: unset;
    }

    @include tablet-s {
      position: absolute;
      top: -104px;
      left: 512px;
    }

    @include desktop {
      position: absolute;
      top: -104px;
      left: 640px;
    }
  }

  .slider-wrapper {
    @include mobile-s {
      transform: translateX(-113px);
    }
  }

  .slick-prev-arrow {
    position: absolute;
    bottom: 0;
    left: 0px;

    @include mobile-s {
      left: 108px;
    }
    @include tablet-s {
      left: unset;
      right: 77px;
    }
    @include desktop {
      left: unset;
      right: 77px;
    }
  }

  .slick-next-arrow {
    position: absolute;
    bottom: 0;
    left: 64px;

    @include mobile-s {
      left: 172px;
    }
    @include tablet-s {
      left: unset;
      right: 13px;
    }
    @include desktop {
      left: unset;
      right: 13px;
    }
  }
}
